.App {

  text-align: center;

  padding: 2% 5%;

}



.App  {

  box-sizing: border-box;

}



ul {

  padding: 0;

  list-style: none;

}



p {

  margin: 5px 0;

}



input[type="text"], input[type="number"] {

  padding: 8px 6px 8px 10px;

  border-radius: 4px;

  outline: none;

  border: 1px solid #ccc;

  font-size: 14px;

  line-height: 20px;

  align-self: center;

  height: 38px;

}



.city {

  width: 250px;

}



.CitySearch {

  display: flex;

  flex-direction: column;

}



.suggestions {

  width: 250px;

  align-self: center;

  margin: 0;

  font-size: 14px;

}



.suggestions > li {

  text-align: left;

  padding: 8px 6px 8px 10px;

  background-color: #eee;

  cursor: default;

}



.suggestions > li:last-child {

  border-bottom-left-radius: 4px;

  border-bottom-right-radius: 4px;

}





.suggestions > li:hover {

  background-color: #BCDCDB;

}



.event {

  padding: 10px 10px 20px;

  margin-bottom: 10px;

  text-align: left;

  border: 1px solid #000107;

  border-radius: 4px;

  position: relative;

}



.details-btn {

  position: absolute;

  right: 5px;

  bottom: 5px;

  padding: 5px 8px;

  border-radius: 4px;

  outline: none;

  cursor: pointer;

}



.name {

  font-size: 18px;

  font-weight: 600;

}



.group-name {

  text-transform: uppercase;

  font-size: 14px;

}



.going, .visibility {

  font-style: italic;

}



.description {

  overflow-x: hidden;

}



.number-of-events {

  width: 80px;

  margin: 20px 0 50px;

  text-align: right;

}